html {
  height: 100%; /* Ensure full height for html */
  overflow-y: scroll; /* Enable vertical scrolling */
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

/* Prevent scroll bar from appearing */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #272b46;
  color: white;
  width: 100vw; /* Full viewport width */
  height: auto; /* Allow height to expand based on content */
  overflow-y: auto; /* Allow vertical scrolling */
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

/* Hide the scrollbar in WebKit browsers */
body::-webkit-scrollbar {
  display: none; /* Hide scrollbar */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Use border-box model for all elements */
* {
  box-sizing: border-box; /* Include padding and border in element's total width and height */
}
