.CreditCards {
    color: white;
    display: flex;
    flex-direction: column;
    gap: 1.6rem;
}

.CreditCards .CreditCard .Label {
    padding: 0;
    margin: 0;
    color: white;
    margin-bottom: 0.4rem;
    font-size: 1.2rem;
    font-weight: bold;
}

.CreditCards .CreditCard .Card {
    width: 100%;
    height: auto;
    aspect-ratio: 1.8;
    background: linear-gradient(to right, #f67e2b, #fe014d);
    border-radius: 0.5rem;
    padding: 0.8rem;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    gap: 0.6rem;
}

.CreditCards .CreditCard .Card p {
    text-transform: uppercase;
    font-stretch: expanded;
    margin: 0;
    padding: 0;
}


.CreditCards .CreditCard .Card .Labels {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.CreditCards .CreditCard .Card .Labels .LabelsP1 {
    font-size: 4vw;
    font-weight: 600;
}

.CreditCards .CreditCard .Card .Labels .LabelsP2 {
    font-size: 6vw;
    font-weight: bold;
}


.CreditCards .CreditCard .Card .CardNumbers {
    flex: 1;
}

.CreditCards .CreditCard .Card .CardNumbers .Numbers {
    font-size: 7.6vw;
    font-weight: bolder;
}

.CreditCards .CreditCard .Card .DateAndBalance {
    display: flex;
    justify-content: space-between;
}

.CreditCards .CreditCard .Card .DateAndBalance .Date {
    font-size: 6vw;
    font-weight: bold;
}

.CreditCards .CreditCard .Card .DateAndBalance .Balance {
    font-size: 6vw;
    font-weight: bold;
}