.login-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* Space out the content */
    align-items: center;
    height: 100vh;
    background-color: #272b46;
    color: white;
}

.login-container .faceID-icon {
    margin-top: 5vw;
    height: 30vw;
    width: 30vw;
    max-height: 16vh;
    max-width: 16vh;
}

.login-container .goBack-icon {
    position: absolute;
    left: 3vw;
    top: 3vw;
    width: 12vw;
    height: 12vw;
    max-height: 7vh;
    max-width: 7vh;
}

.login-container .logo-icon {
    position: absolute;
    right: 3vw;
    top: 3vw;
    width: 12vw;
    height: 12vw;
    max-height: 7vh;
    max-width: 7vh;
}

.pin-display {
    font-size: 2rem;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 5vw;
    box-sizing: border-box;
}

.number {
    height: 2.2rem;
    width: 2.2rem;
    max-width: 10vw;
    max-height: 10vw;
    border-radius: 50%;
    border: 2px solid gray;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: all ease-in-out 0.12s;
}

.empty {
    border-color: gray;
}

.filled {
    background-color: #f68b23;
    border-color: #f68b23;
}

.shake {
    animation: shake 0.5s ease forwards;
}

.shake .filled {
    background-color: #fd004d;
    border-color: #fd004d;
}

@keyframes shake {
    0% {
        transform: translateX(0);
    }

    25% {
        transform: translateX(-10px);
    }

    50% {
        transform: translateX(10px);
    }

    75% {
        transform: translateX(-10px);
    }

    100% {
        transform: translateX(0);
    }
}

.keypad {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 20px;
}

.keypad-row {
    display: flex;
    justify-content: center;
    gap: 1vw;
}

button {
    background-color: #333c5d;
    color: white;
    border: none;
    border-radius: 50%;
    max-width: 12vh;
    max-height: 12vh;
    width: 22vw;
    height: 22vw;
    margin: 1.6vw;
    font-size: 2.5rem;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

button:hover {
    background-color: #333c5d85;
}

.icon-button {
    background: none;
    /* No background for icon buttons */
    border: none;
    /* No border for icon buttons */
    padding: 0;
    /* No padding */
}

.icon-button:hover {
    background-color: transparent;
}

.icon {
    width: 12vw;
    height: 12vw;
    max-height: 7vh;
    max-width: 7vh;
}