.welcome-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    background-color: #272b46;
    color: white;
}

.welcome-container #logo {
    margin-top: 5vh;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#logo img {
    max-width: 50vh;
    max-height: 50vh;
    width: 50vw;
    height: 50vw;
}

#logo h1 {
    text-align: center;
    font-size: 3rem;
    padding: 0;
    margin: 0;
    text-transform: uppercase;
    line-height: 0.8; /* Adjust this value for line height */
}

#logo h1 span {
    padding: 0;
    margin: 0;
    color: #fc014f;
    font-size: 2rem;
}

.login-button {
    padding-bottom: 2vw;
    width: 70vw;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 8vh;
    background-color: #fc014f;
    color: white;
    border: none;
    border-radius: 8px;
    text-decoration: none;
    font-size: 2rem;
}

.login-button:hover {
    background-color: #a50236;
}