.transaction-list {
    background-color: #3a466d;
    color: white;
    border-radius: 0.5rem;
    overflow: hidden;
}

.transaction-list .transaction-Title {
    background-color: #3a466d;
    padding: 1rem;
    border-bottom: 2px #5b628d solid;
}

.transaction-list .transaction-Title .Title {
    margin: 0;
    padding: 0;
    font-size: 6vw;
}


.transaction-list ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    padding: 0;
    gap: 0;
    background-color: #343d5e;
}

.transaction-list li {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    gap: 0.3rem;
    padding: 1rem;
    border-bottom: 2px #5b628d solid;
}

.transaction-list li:last-child {
    border: none;
}

.transaction-list li .Data {
    text-align: start;
    display: flex;
    justify-content: space-between;
}

.transaction-list li .Data .Description {
    font-size: 5vw;
    margin: 0;
    padding: 0;
    font-weight: 700;
}

.transaction-list li .Data .Amount {
    text-align: end;
    font-size: 5vw;
    margin: 0;
    padding: 0;
    font-weight: 700;
}

.Data .income {
    color: #2668fd;
}

.Data .expense {
    color: #fc004f;
}

.transaction-list li .Info {
    text-align: end;
    display: flex;
    justify-content: space-between;
}

.transaction-list li .Info .text {
    font-size: 4vw;
    margin: 0;
    padding: 0;
    color: rgba(255, 255, 255, 0.5);
}