nav {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 18vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #38446e;
    padding: 0 1rem;
    box-sizing: border-box;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    gap: 1.5rem;
    width: 100%;
    margin: 0;
    padding: 0;
}

a {
    color: white;
    text-decoration: none;
    display: flex;
    align-items: center;
}

.nav-icon {
    width: 12vw;
    height: 12vw;
}